import { DashboardLayoutComponent } from "@syncfusion/ej2-react-layouts";
import * as React from "react";
import "./App.css";

export default class App extends React.Component {
  constructor() {
    super(...arguments);
    this.cellSpacing = [10, 10];
    this.resize = ["e-south-east", "e-east", "e-west", "e-north", "e-south"];
    this.panels = [
      {
        sizeX: 5,
        sizeY: 3,
        row: 1,
        col: 0,
        content: '<div className="content">3</div>',
      },
      {
        sizeX: 2,
        sizeY: 3,
        row: 0,
        col: 1,
        content: `<div className="content-1"><ul>
        <li>Form</li>
        
      </ul>
      </div>`,
      },
      {
        sizeX: 3,
        sizeY: 3,
        row: 0,
        col: 4,
        content: `<div className="content"><form >
        <label for="fname">First name:</label><br>
        <input type="text" id="fname" name="fname" value="John"><br>
        <label for="lname">Last name:</label><br>
        <input type="text" id="lname" name="lname" value="Doe"><br><br>
        <input type="submit" value="Submit">
      </form> </div>`,
      },
    ];
  }
  onResizeStart() {
    console.log("Resize start");
  }
  //Dashboard Layout's drag event function
  onResize(args) {
    console.log("Resizing");
  }
  //Dashboard Layout's dragstop event function
  onResizeStop(args) {
    console.log("Resize stop");
  }
  render() {
    return (
      <div>
        <div id="container">
          <DashboardLayoutComponent
            id="defaultLayout"
            cellSpacing={this.cellSpacing}
            panels={this.panels}
            allowResizing={true}
            columns={5}
            resizableHandles={this.resize}
            resizeStart={this.onResizeStart.bind(this)}
            resize={this.onResize.bind(this)}
            resizeStop={this.onResizeStop.bind(this)}
          />
        </div>
      </div>
    );
  }
}
